import React from 'react';
import video from '../assets/texas-attract-loop-v2.mp4';

const attractLoopStyles = {
  attractLoop: {
    position: 'relative',
    background: 'gray',
    height: '100vh',
    width: '100%',
    overflow: 'hidden',
    zIndex: '2'
  },
  attractVideo: {
    height: '100%',
    width: '100%',
    position: 'absolute',
    overflow: 'hidden',
    zIndex: '2'
  },
  video: {
    minWidth: '100%',
    minHeight: '100%',
  }
}

const AttractLoop = () => {
  return (
    <div>
      <a href="/" style={ attractLoopStyles.attractVideo }>
        <video playsInline autoPlay muted loop style={ attractLoopStyles.video }>
          <source src={video} />
        </video>
      </a>
    </div>
  )
}

export default AttractLoop;


