import React, { Component } from 'react';
import { Container, Fade } from 'reactstrap';
import './Home.css';
import tfaLogo from '../assets/tfa-logo-orig.png';

class Home extends Component {
  state = { fadeIn: true }
  render() {
    return (
      <div className="home">
        <Fade in={this.state.fadeIn }>
          <Container>
            <img src={tfaLogo} alt=""/>
            <p>The Texas Forestry Hall of Fame honors both deceased and living persons who made substantial and lasting contributions to forestry and forest conservation in the state.</p>
          </Container>
        </Fade>
      </div>
    );
  }
}

export default Home;


