module.exports = [{"name":"William T. Carter","dates":"1856-1921","inducted":2001,"image":"carter-william-t.jpg","description":"<p>W.T. Carter was born in Tyler, Texas in 1856. His lumber empire began in 1876 with a small mill in Trinity and grew through a succession of additional and larger mills. W.T. and brother and partner E.A. Carter formed the premier mill of the firm in 1897, W.T. Carter &amp; Bro., at what became Camden. Carter constructed a railroad, the Moscow, Camden, and San Augustine Railroad in 1898. With George Kelley, W.T. Carter built the Carter Kelley Lumber Company in Manning in 1906.<br />W.T. Carter was among the early leaders in forest management techniques. His life and career spanned a time of significant change in the timber industry, and for most of his years he was at the forefront of modern business practices.</p>"},{"name":"Simon W. Henderson, Sr.","dates":"1859-1923","inducted":2001,"image":"henderson-simon-wood-sr.jpg","description":"<p>Simon W. Henderson, Sr. came to Texas in 1883 from Georgia. In Polk County, Henderson worked for a tie-cutting contractor, and within a year purchased the business. He acquired a general mercantile in 1886, was active in timber and land sales, and invested with Joseph H. Kurth in a sawmill in Keltys.<br />Throughout his career, Henderson was involved in numerous sawmill ventures throughout East Texas and Louisiana, and in the Angelina &amp; Neches River Railroad, a company-owned line of Angelina County Lumber Company. His business acumen led him into many other related enterprises in the Pineywoods region, including Lufkin Foundry &amp; Machine Company.</p>"},{"name":"John H. Kirby","dates":"1860-1940","inducted":2001,"image":"kirby-john-h.jpg","description":"<p>John H. Kirby was the most widely known Texas lumberman during his lifetime. His law practice involved East Texas timberlands and he began to purchase timber himself, and with other capital formed several timber and milling companies.<br />After selling his railroad to the Atchison, Topeka, and Santa Fe Railway Company in 1901, Kirby and others formed the Kirby Lumber Company and Houston Oil Company. He operated more than a dozen sawmills that produced over a million board feet of lumber a day, making him the largest lumber manufacturer in the Gulf Southwest. He was among the earliest advocates of forest conservation, developing one of the first forest working plans in Texas.</p>"},{"name":"Joseph H. Kurth","dates":"1857-1930","inducted":2001,"image":"kurth-joseph-h.jpg","description":"<p>Joseph H. Kurth, born in Germany in 1857, immigrated to Texas in 1878. Initial work in a commercial lumber yard led to the purchase of a mill at Kurth Station, near Corrigan in Polk County, and eventually to the purchase of a larger mill at Keltys, north of Lufkin, along with Simon W. Henderson, Sr. Angelina County Lumber Company was considered one of the most complete and modern of its time. Angelina &amp; Neches River Railroad also resulted from the enterprise.<br />Kurth expanded interests across East Texas and into western Louisiana, becoming very influential in the southern forest products industry.</p>"},{"name":"Henry J. Lutcher","dates":"1836-1912","inducted":2001,"image":"lutcher-henry-j.jpg","description":"<p>H.J. Lutcher began his lumbering career in Pennsylvania in the early 1860s in partnership with G. Bedell Moore. Their lumber manufacturing business prospered into the mid-1870s when dwindling timber resources brought the pair to Texas in search of a promising new location.<br />In 1878, they established Lutcher-Moore Lumber Company, the first of the large mills that ushered in the boom years of the industry. Mills at Orange and in Louisiana were among the largest and finest for their time, firmly establishing the Lutcher &amp; Moore Lumber Company in the economic fabric of Texas.</p>"},{"name":"G. Bedell Moore","dates":"1840-1908","inducted":2001,"image":"moore-g-bedell.jpg","description":"<p>G. Bedell Moore partnered with H.J. Lutcher in the lumber manufacturing business in Pennsylvania. They prospered for several years in the early 1860s, but eventually the resource dwindled, and the pair elected to move to Texas in 1877.<br />Impressive virgin forests of long leaf pine and cypress, suitable waterways for rafting logs and transporting lumber to coastal markets, and rail transportation in the area led to the establishment of Lutcher &amp; Moore Lumber Company in Orange in 1878. The inception of this, the largest mill of its day in Texas, and other subsequent mills in Texas and Louisiana, helped lead the way for the other timber pioneers who followed.</p>"},{"name":"Thomas L. L. Temple","dates":"1859-1935","inducted":2001,"image":"temple-t-l-l.jpg","description":"<p>T.L.L. Temple arrived in Texarkana via Arkansas by 1877. There, he worked in a commissary, and within ten years was partner in a sawmill operation in Atlanta. In 1893, Temple formed Southern Pine Lumber Company and purchased several thousand acres of land along the Houston, East &amp; West Texas Railway in Angelina County, establishing the town of Diboll.<br />Temple organized the Texas South-Eastern Railroad Company at Diboll in 1900 and Temple Lumber Company at Pineland in 1910. He managed his sawmill towns and companies with a sense of permanency and was considered progressive for his time, with land management programs as early as the 1910s.</p>"},{"name":"Arthur Temple, Jr.","dates":"1920-2006","inducted":2002,"image":"temple-arthur-jr.jpg","description":"<p>Arthur Temple, Jr. was born in 1920 in Texarkana, Arkansas, into a family already very active in the timber industry in the South. He began his career in 1938, assumed the position of executive vice-president of Southern Pine Lumber Company in 1948, and subsequently served as President and CEO and Chairman of the Board of Temple-Eastex, Inc. and Temple Industries, Inc., and Chairman of the Board of Temple Inland, Inc. Arthur Temple led the company through its transitions to Temple-Inland, Inc. including a merger with Time Incorporated.<br />Temple's accomplishments are varied and noteworthy. He is credited with leading not only Southern Pine Lumber Company and its successors in constant mill modernization, progressive conservation and management practices, and product and company development and diversification, but also with providing visionary leadership for the entire southern forest products industry. He has been vitally interested in the lives of the citizens of Diboll, Angelina County, and East Texas.</p>"},{"name":"Dr. Laurence C. Walker","dates":"1924-1999","inducted":2002,"image":"walker-laurence.jpg","description":"<p>Laurence C. Walker, an inner city Washington D.C. product, acquired his love of the forest and outdoors from Scouting. Walker completed degrees in Forestry from Penn State University, Yale University, and finally his Ph.D. from State University of New York at Syracuse. Dr. Walker's professional career included working as a forester on the Sabine National Forest in Texas, doing research on longleaf pine at the US Forest Service's Southern Forest Research Station, and teaching at the University of Georgia School of Forestry.<br />In 1963, Dr. Walker came to Stephen F. Austin State Teacher's College as Head of the Forestry Department. Under Dr. Walker's guidance, the forestry program grew from a small, undergraduate curriculum to one of the largest and most highly respected Forestry Schools in the nation. He was named the Lacy Hunt Professor of Forestry after his tenure as Dean of the School of Forestry.</p>"},{"name":"Eric O. Siecke","dates":"1882-1974","inducted":2002,"image":"siecke-eric-o.jpg","description":"<p>E.O. Siecke was born and educated in Nebraska, receiving his degree in forestry at the University of Nebraska. Early professional work included service as Deputy State Forester in Oregon. Siecke assumed duties as State Forester of Texas in 1918, and served until his retirement in 1942. He succeeded in putting together an organization, which advanced the causes of forestry conservation and education in the state of Texas.<br />During the time Siecke headed the Texas Forest Service, extensive programs in fire control and education were developed. Forest management training and education were a major focus for the agency under Siecke. The Texas Forest Service instituted seed nurseries to help fulfill the increasing demands for seedlings as companies increased reforestation efforts. The first state forest was purchased in 1924 under Siecke's leadership. It was renamed the E.O. Siecke State Forest in 1951.</p>"},{"name":"Ed R. Wagoner","dates":"1918-2006","inducted":2003,"image":"wagoner-ed-r.jpg","description":"<p>Ed Wagoner, born in Riverside, Wyoming, received degrees in forestry from the University of Georgia and Duke University. First employed as District Forester in the Kentucky Division of Forestry, Wagoner moved to Texas in 1949 to become Head of the Education Section of the Texas Forest Service. In 1955 he was named executive vice president of the Texas Forestry Association, where he continued his employment until his retirement in 1983.<br />During his career, Wagoner was a voice for forestry in Texas. He was named a Fellow in the Society of American Foresters, and was Man of the Year in Texas Agriculture in 1978. He was instrumental in founding the Texas Forestry Museum, and the Texas Reforestation Foundation, as well as being active in the Texas Christmas Tree Growers Association. He also served as business manager for the Texas Society of American Foresters.</p>"},{"name":"Dr. Robert Baker","dates":"1927-2017","inducted":2003,"image":"baker-robert.jpg","description":"<p>Robert Baker was born in 1927 in California, where he received his undergraduate degree and his Masters in Forestry at the University of California. He then completed his education with a Ph.D. in Forest Valuation at the State University of New York. Baker assumed teaching responsibilities at Stephen F. Austin State University in Nacogdoches in 1956, where he remained until moving to Texas A&amp;M University in 1975. There he received the Teaching Excellence award for the A&amp;M Department of Forest Science.<br />During his more than 50-year career in forestry, Robert Baker produced numerous articles, papers and presentations in his field. Dr. Baker's professional expertise lies in forest valuation and in remote sensing and photo interpretation, but he also has a keen interest in forest history. A significant contribution to Texas forest history is Sawdust Empire: The Texas Lumber Industry, 1839 - 1940, co-authored with Dr. Robert Maxwell.</p>"},{"name":"Dr. Robert Maxwell","dates":"1911-1990","inducted":2003,"image":"maxwell-robert.jpg","description":"<p>Robert Maxwell received his BA degree in history in his home state of Kentucky, and then obtained his masters from the University of Cincinnati and his Ph.D. from the University of Wisconsin. Maxwell taught at Wisconsin and at the University of Kentucky before beginning his tenure at Stephen F. Austin State University in 1952. In 1966, he was named the first SFA Alumni Association's Distinguished Professor.<br />Dr. Maxwell became fascinated with the history of the economic development of East Texas, in particular railroads and lumbering. He published many books and papers on these topics, including Whistle in the Pineywoods and Sawdust Empire: The Texas Lumber Industry, 1839 - 1940, co-authored with Robert Baker. Through Dr. Maxwell's efforts, many documents relating to early forest industries were saved from destruction.</p>"},{"name":"W. Goodrich Jones","dates":"1860-1950","inducted":2004,"image":"jones-w-goodrich.jpg","description":"<p>William Goodrich Jones is considered the Father of Forestry in Texas. Jones, a banker in Temple, Texas, had a special love for trees. During his youth the family lived in Germany, where Jones visited with rangers and forestry workers in the Black Forest. Back in Texas, W. Goodrich Jones promoted the management techniques he had seen as a boy. In 1889 B.E. Fernow, chief of the US Bureau of Forestry, enlisted Jones to make a forest survey of the Pineywoods of Texas.<br />W. Goodrich lobbied lumbermen and the legislature to create what would become the Texas Forest Service in 1915. A major force was the Texas Forestry Association, spearheaded by Jones in 1914. He served as president of TFA until 1921. Jones also promoted a state Arbor Day designation.<br />Jones was born in New York in 1860. He earned a degree in business from Princeton, and started in the banking business in Galveston before moving to Temple. W. Goodrich Jones died in 1950.</p>"},{"name":"Alfred D. Folweiler","dates":"1902-1986","inducted":2005,"image":"folweiler-alfred-d.jpg","description":"<p>A.D. Folweiler studied forestry at Penn State University, Yale, and the University of Wisconsin. Folweiler's work before coming to Texas included the U.S. Forest Service and several state forestry organizations. As a faculty member at Louisiana State University, Dr. Folweiler wrote his widely used textbook, Forest Fire in the United States. After World War II he worked with International Paper and was active in the formation of the Louisiana Forestry Association.<br />Dr. Folweiler was named Director of the Texas Forest Service in 1949, holding the position until his retirement in 1967. Folweiler formed the first forest tree improvement program in the South. The Texas Forest Service achieved an outstanding record in forest fire prevention and control under his guidance. He also fostered what would become county and regional forest landowner associations in Texas. Folweiler was active in the Texas Forestry Association and served as president of the National Association of State Foresters. He was a Fellow in the Society of American Foresters.</p>"},{"name":"Ernest L. Kurth, Jr.","dates":"1885-1960","inducted":2005,"image":"kurth-ernest-l-jr.jpg","description":"<p>Ernest L. Kurth, Jr. attended Southwestern University before joining the family business, Angelina County Lumber Company, in Keltys Texas in 1905. Kurth worked in several positions at the sawmill, learning firsthand the challenges of each job. In 1936 he became president of the company. He began several new lumber enterprises in Texas and Louisiana, and was active in several other regional businesses. He is perhaps best known for his leadership in the organization of Southland Paper Mills in Lufkin, the first mill to use southern yellow pine to manufacture newsprint.<br />Kurth worked hard for forestry in Texas. He supported the Texas Forestry Association in fighting for the institution of the Texas Forest Service in 1915. Kurth was perhaps one of the first in Texas to experiment with reforestation, planting longleaf seeds in 1925. He followed the advice of the fledgling Texas Forest Service in developing selective cutting and sustained-yield programs. In 1937 Kurth hired the first graduate industrial forester in Texas.</p>"},{"name":"J. Brady Wadsworth","dates":"1922-2003","inducted":2006,"image":"wadsworth-brady.jpg","description":"<p>Brady Wadsworth, a 1958 University of Florida graduate in forestry, began his career with ITT Rayonier, Inc., but moved to Texas to work with Southland Paper Mills in 1969. He served as Vice President of Forest Resources from 1972 to 1977. In 1985, Wadsworth retired from the St. Regis mill as Regional Manager of the Texas Region. Brady served on the Executive Committee of the American Pulpwood Association, the Board of Directors of the Forest Farmers Association, and as President of the Texas Forestry Association and the Texas Forestry Museum.<br />Brady Wadsworth's passion for planting trees to keep pace with the harvest rate led to the formation of the Texas Reforestation Foundation (TRe) in 1980. He persuaded industry leaders to voluntarily contribute funds to TRe, a cost-sharing program for reforestation of private forest land. Wadsworth served as the first president of TRe. To date, nearly 187,000 acres have been replanted under the program, with an investment of $8.4 million from TRe.</p>"},{"name":"Glenn A. Chancellor","dates":"1937-2001","inducted":2007,"image":"chancellor-glenn-a.jpg","description":"<p>Glenn Chancellor, graduated in 1959 from Stephen F. Austin State University with a Bachelor of Forestry degree. He lived most of his life and spent his career in the piney woods of East Texas that he loved. He worked in Florida at Container Corporation, prior to returning to east Texas to work for Southwestern Settlement and Development, a predecessor company of Temple-Inland, Inc. He retired after 38 years as group vice president from Temple-Inland, Inc. in Diboll. In this capacity he also served as vice president of Inland Container Corporation and co-chief executive officer of Planforsur in S.A. Mexico.<br />Glenn Chancellor's professional affiliations reflect his lifelong dedication to the forestland and forestry industry. He served on executive committees and as chairman of the American Pulpwood Association, the American Forest Council, the Texas Forestry Museum, the Society of American Foresters (Distinguished Service Award - 1984), and Texas Forestry Association past president. He was a founding member and past chairman of Texas Reforestation Foundation, and on the Board of Trustees for The Nature Conservancy of Texas. Glenn also served on the Board of Directors for the Stephen F. Austin Alumni Association.</p>"},{"name":"Charles R. Franklin","dates":"1918-2008","inducted":2008,"image":"franklin-charles-r.jpg","description":"<p>Charles R. Franklin, the youngest of five children graduated from University of Georgia in 1939 with a degree in Forestry. He served in the U.S. Navy from 1942-1948 then took a position in Texas as forester and land manager with Wirt Davis Estate managing 60,000 acres of timberland. His mission was to grow forests continuously on every possible acre of Wirt Davis land and to provide a sustained and increasing timber yield. Under his care the Estate's timber productivity increased by 500% and today its forests continue to grow more timber than is harvested every year. He retired from Wirt Davis in 2000, after 42 years of dedication.<br />Charles's recognitions include Best in Trees/Timberland Management in 1998 by the Ft. Worth Star Telegram, He became a Fellow in the Society of American Foresters in 1979, and received the DSF award in 1972, Outstanding Forester of Texas - 1972 by TFA, and served as vice president of the Texas Forestry Association. Charles was one of the first to be licensed as a Registered Professional Land Surveyor in Texas. He was tireless in his civic, community and church organizations also. In 1992 he received the Melvin Jones Award for Dedicated Humanitarian Services from the Lions Club International.</p>"},{"name":"Bruce Miles","dates":"1935-2017","inducted":2008,"image":"miles-bruce.jpg","description":"<p>Bruce R. Miles graduated with a Bachelor of Forestry degree in 1958, through a cooperative scholarship program between Texas A &amp; M University and Louisiana State University. After serving as an officer in the U.S. Field Artillery, he joined the Texas Forest Service in 1959, and rose through the ranks from assistant forester to director of the agency in 1981. He served as the 6th State Forester of Texas until his retirement in 1996, whereupon he was bestowed the prestigious title of Director Emeritus by the Texas A &amp; M System. Miles was a delegate of the United States to the IX World Forestry Congress in Mexico City in 1986 and is a member of the Mexico-Texas Exchange Commission. During his 37-year career he was responsible for many innovative forest management programs including the enactment of an industry-funded tree planting program, the Texas Reforestation Foundation, or TRe. He was responsible for the development of a statewide wildfire control program, and an effective pest/beetle control program.<br />Bruce's awards include: Gifford Pinchot Medal in 1997 by the Society of American Foresters, Distinguished Service to Forestry Award from the Texas Society of American Foresters in 1981, and the Distinguished Performance Award for Administration in 1982 from the Texas A &amp; M System.</p>"},{"name":"Kenneth Nelson","dates":"1915-1992","inducted":2008,"image":"nelson-kenneth.jpg","description":"<p>Kenneth Nelson, born in Diboll, Texas graduated from Lufkin High School in 1932. Although his higher education was limited to college night courses and a correspondence course from American School in Drafting, his self-taught lessons in the field made him expert in the industry and an innovative leader. He joined Southern Pine Lumber Company in 1932, as an office clerk, in the Land &amp; Timber Department. In 1946 became the company's first forester. Nelson, a Registered Public Surveyor, brought an inherent understanding of the principles of ecology. His concept of the perpetual forest was in stark contrast to the cut-out-and get-out philosophy that branded the industry in the early 1900's. Nelson's recognition that trees are a crop, inviting weeding and thinning was accepted as company doctrine. He retired from Temple-Inland as Vice President of the Forestry Division in 1980, after 48 years.<br />Kenneth Nelson's professional affiliations reflected his dedication. He was a past president of the Texas Surveyors Association, the Texas Forestry Association, and of the board of the Texas Forestry Museum, a member of the Society of American Foresters, and he was honored by the Arthur Temple College of Forestry at Stephen F. Austin State University with the Kenneth Nelson Distinguished Professorship filled in June 2000.</p>"},{"name":"John Courtenay","dates":"1922-2017","inducted":2009,"image":"courtenay-john.jpg","description":"<p>John Courtenay received his BS degree from New York State College of Forestry (Syracuse University) in 1948. He served in the U.S. Navy during World War II. He began and ended his professional career with the U.S. Forest Service, serving on national forests in Tennessee, Mississippi, and Alabama. John moved to supervisory positions with the National Forests in Florida and the U.S. Forest Service Regional office in Atlanta, Georgia. In 1966, John moved to Lufkin to assume the role of the Supervisor of National Forests in Texas, where he served until his retirement in 1980.<br />Throughout his career and retirement, John's dedication to his profession is reflected in his long list of accomplishments and affiliations. He has been a member of the Society of American Foresters for over 50 years, being named a Fellow in 1984. John remains active in The Texas Forestry Association and the Texas Forestry Museum. He served as an active volunteer almost from the beginning of the Museum and eagerly shares his vast knowledge of East Texas Forest History. He was a Charter member and past chair of the Lufkin Tree Board and has been active in Boy Scout Troop 140 in Lufkin for many years.</p>"},{"name":"Clyde Thompson","dates":"1899-1987","inducted":2010,"image":"thompson-clyde.jpg","description":"<p>Clyde Thompson began his long career with Southern Pine Lumber Company on October 1, 1916, as a stenographer. For more than seventy years he served that company and its successors, Temple Industries, Inc., and Temple-Eastex, Inc. Thompson's assignments included lumber orders processor, purchasing agent, insurance claims handler, superintendent of logging operations, and retail yards supervisor. He was a director of the Texas South-Eastern Railroad from 1938 through 1987. In 1951 he was named a vice president and director of Southern Pine Lumber Company. After his official retirement in 1976, Thompson continued his efforts in the company's behalf as secretary and treasurer of the Temple Political Action Committee.<br />Clyde Thompson was very active in affairs affecting his community and state, and the forest industry in general. From 1962 until 1975, Thompson served as the first mayor of Diboll, Texas. He was appointed as a member of the Board of Regents of Texas A&amp;M University in 1961, and served six years. Thompson was president of the Texas Forestry Association from 1959 through 1960 and was a leader in the Southern Logging Congress, the Texas Logging Association and the Texas Lumber Manufacturing Association.</p>"},{"name":"Melvin Kurth, Jr.","dates":"1931-Present","inducted":2011,"image":"kurth-melvin-jr.jpg","description":"<p>After growing up in Houston, Melvin E. Kurth, Jr., received a mechanical engineering degree from Southern Methodist University in 1955. After working as a consulting engineer in Houston, he went to work for Southland Paper Mills in 1957, eventually rising to President and CEO prior to leaving the company in 1978. He served as President of the Angelina &amp; Neches River Railroad from 1974 to 1980. Beginning in 1968, he served as a director of Lufkin Industries. He also has served as a director of St. Regis Paper Company, First City National Bank of Houston, and Lufkin National Bank.<br />In 1975, he served as chair of the fundraising drive committee for the creation of the Texas Forestry Museum and as chair of the Air and Water Conservation Committee of the Houston Chamber of Commerce. In 1976, he was president of the Southern Forest Institute. In 1977, he was chair of the Newsprint Division of the American Paper Institute. In 1979, he served as board president of Memorial Hospital in Lufkin. He also has served as a director of the East Texas Chamber of Commerce, Memorial Hospital System of Houston, and Southwestern University of Georgetown. He currently serves as a director of the Pineywoods Foundation in Lufkin. Since retiring, he has operated a hunting and fishing ranch with his wife, Paula, near Chama, New Mexico.</p>"},{"name":"Jim Webster","dates":"1926-1993","inducted":2011,"image":"webster-jim.jpg","description":"<p>Born in Bryan, Texas, Jim Webster received his BS in Forestry at the University of Michigan in 1949. A second-generation forester, he worked for the Texas Forest Service as a district forester from 1949 to 1959. In 1959, he became chief forester for the Foster Lumber Company. In 1962, he went to the Kirby Lumber Company as manager of corporate relations. He retired from the company as director of governmental relations in 1986. As a member of the Society of American Foresters, he served as the Texas chapter chair in 1957 and 1958, he was awarded the Gulf States Distinguished Service Award in 1974, and he was honored as a Fellow in 1984.<br />While serving on the American Forest Institute board of directors, Jim Webster was responsible for bringing Project Learning Tree to Texas. The program has trained thousands of educators through the years. He was President of Texas Forestry Association in 1973 and 1974, and served as the first chair and founding member of the Texas Forestry Association's Forestry Political Action Committee. He also served on the board of directors at the Louisiana Forestry Association. He was active in several other forestry organizations. Upon his retirement, he was elected Conroe Municipal Judge in 1992 and held that position until his death in December of 1993.</p>"},{"name":"David C. Kenley","dates":"1886-1975","inducted":2012,"image":"kenley-david-c.jpg","description":"<p>Dave Kenley, a trained land surveyor, headed the Southern Pine Lumber Company Land and Timber Department for sixty years. He was responsible for negotiations for the acquisition and disposal of timber holdings, including sale of acreage to the federal government for parts of the Sabine National Forest.<br />Kenley was proactive in dealing with squatters on company land. He introduced cattle grazing and fencing on company lands to protect them from adverse possession. His efforts were also instrumental in reducing poaching, and developing guidelines for hunting on Temple's holdings. In the early 1900's, Kenley surveyed all of the company's land.<br />Kenley served as president of the Texas Forestry Association in 1939, and was named as a member of the Who's Who of Sam Houston State University.</p>"},{"name":"Joe C. Denman, Jr.","dates":"1923-2014","inducted":2013,"image":"denman-joe-c.jpg","description":"<p>Joe Denman received his Bachelor of Architecture degree from Texas A&amp;M University in 1950. He served in the U.S. Navy during World War II as a Naval Aviator and Assistant Squadron Administrative Officer. He began his career as a professional engineer with Southern Pine Lumber Company. Joe filled many positions with the company as it evolved into Temple Industries, Time Incorporated, Temple-Eastex Incorporated, and Temple-Inland Incorporated. Mr. Denman became Chairman of the Board, as well as President and CEO of Temple-Eastex.<br />Throughout his career, Joe's dedication to his profession and the forest industry is reflected in his long list of accomplishments and affiliations. He has been an active member and director of the Southern Forest Products Association, Texas Forest Products Manufacturers Association, National Forest Products Association, and the Forest Products Research Society. He has served and directed many more professional and civic associations.</p>"},{"name":"Marvin G. Angle","dates":"1910-1983","inducted":2014,"image":"angle-marvin-g.jpg","description":"<p>Marvin G. Angle was born in Courtland, Kansas in 1910. He graduated with a BS in Forestry in 1936 from Oregon State School of Forestry. In 1934, Mr. Angle began his career with the U.S. Forest Service and the Texas A&amp;M Forest Service until 1946. At that time, he accepted a position with International Paper and began acquiring and managing thousands of acres to support company processing plants and operations, including land for the seed orchard in Nacogdoches County and the nursery operation in Bullard, Texas. He was also successful in purchasing the construction site for the paper mill near Texarkana, Texas. At the time of his retirement in 1975, Mr. Angle held the position as Area Forest Superintendent.<br />Marvin Angle was active and held many positions in the Texas Society of American Foresters; Texas Forestry Association, including president in 1961-1963; East Texas Chamber of Commerce; and the Westminster Presbyterian Church in Nacogdoches.</p>"},{"name":"Thomas G. Burch","dates":"1944-2017","inducted":2014,"image":"burch-thomas-g.jpg","description":"<p>Thomas G. Burch was born in Jasper County in 1944. At the age 20, he began his self-employed career with his own logging business, B&amp;W Logging Contractors, Inc. For five decades, Mr. Burch worked tirelessly to provide for his family and improve the forest industry. He was an industry pioneer by operating a new biomass fuel chipping operation, and an in-woods clean chipping operation. Also, Burch organized loggers, gave them a unified voice, promoted their causes, and enhanced public perception of the industry.<br />Mr. Burch was a founding member and original Chairman of the Texas Logging Council, which helped secure Texas loggers with a fully mechanized workers' compensation insurance rate. Mr. Burch received several awards including Logger of the Year, Logging Business of the Year, and Activist of the Year. He served in many capacities with the Texas Forestry Association, including President in 1999; Texas Logging Council; and several civic organizations.</p>"},{"name":"John O. Burnside","dates":"1906-1978","inducted":2014,"image":"burnside-john-o.jpg","description":"<p>John Joe O. Burnside was a native of West Virginia and graduated from Colorado State University in 1932 with a B.S. in Technical Forestry. His early professional work began with the U.S. Forest Service, where he served in Colorado, Wyoming, Arkansas, and finally in Texas on the Angelina and Sam Houston National Forests. In 1935, he was in charge of several crews of the Civilian Conservation Corps (C.C.C.), building roads throughout remote areas of East Texas, which made forest fire protection more efficient.<br />In 1936, Mr. Burnside served in the Division of Forest Protection with the Texas A&amp;M Forest Service. By 1949, he became the Chief of the Division of Forest Protection, and for 29 years, was responsible for protecting 9 million acres of timberland from wildfire. Burnside had a hand in developing many methods of detecting and fighting forest fires in the South. He was responsible for working with the U.S. Weather Bureau to initiate daily forecasts pertinent to forest fire hazards for control agencies. Burnside retired from the Texas A&amp;M Forest Service after 36 years of dedicated service to the state of Texas.</p>"},{"name":"Ronald H. Hufford","dates":"1948-Present","inducted":2015,"image":"hufford-ronald-h.jpg","description":"<p>Ron Hufford serves as the Executive Director for the Texas Forestry Association (TFA) from 1984 to present. He established the Texas Forest Landowners Council and the Texas Logging Council, which unified the efforts of their members and contributed to TFA's legislative accomplishments.<br />Under Hufford's leadership, TFA played an important role with the establishment of educational programs like Project Learning Tree and the Teacher Conservation Institute. He served as chairman for the Texas Society of American Foresters in 2003 and the Texas Agriculture Council from 2006-2011.<br />Hufford graduated from Southern Illinois University with a Bachelor's Degree in Forestry. He is an Eagle Scout, and he served in the United States Air Force. Hufford is an active member of First Christian Church and the Lufkin Rotary Club. He is married to Shelly Hufford; they have two children and one grandchild.</p>"},{"name":"Carol Riggs","dates":"1945-Present","inducted":2015,"image":"riggs-carol.jpg","description":"<p>Carol Riggs served as the Director of the Texas Forestry Museum from 1986 - 2007. She was instrumental in molding a museum that protects and preserves the history of the Texas forest industry. She created many museum exhibits, which present history for new generations. Riggs also established the Texas Forestry Hall of Fame to honor those who made a great impact on the forest industry.<br />Riggs created a collection database to organize the museum's artifacts and the Sawmill Database, which provides easy access to researchers of Texas sawmills. She also organized children's museum events like EEK Week and Reading Railroad.<br />Riggs graduated from Arizona State University with a Bachelor's Degree in Biology. She is an active member of First Christian Church and the Kiwanis Club. She is married to Jim Riggs; they have six kids and many grandchildren and great-grandchildren.</p>"},{"name":"Paul F. Hersey","dates":"1908-1980","inducted":2015,"image":"hursey-paul-f.jpg","description":"<p>Paul Hursey was the first commercial forester employed in Texas when he joined the Angelina County Lumber Company as the Chief Forester in 1937. He initiated the program of selective logging and industrial reforestation.<br />Hursey also played an active role in the establishment of the Southland Paper Mill, and he secured the raw materials used in the testing process, which proved that Southern Yellow Pine could be used to manufacture newsprint. Later, Hursey was in charge of the sawmill operations and timberland holdings of the Kurth Lumber Company in Jasper.<br />Hursey graduated from the University of Michigan with a Bachelor's Degree in Forestry in 1937. Hursey served as president of the Texas Forestry Association in 1951 and director of the Texas Lumber Manufacturers Association. Hursey was married to Floreen Rowe Hursey and they had three children.</p>"},{"name":"Dr. R. Scott Beasley","dates":"1942 - Present","inducted":2016,"image":"beasley-r-scott.jpg","description":"<p>Dr. Scott Beasley served as the Dean of the Arthur Temple College of Forestry at Stephen F. Austin State University from 1992 to 2008.  His leadership as dean enhanced the reputation of SFASU and influenced the professional development of many students and faculty. <br /> Under Dr. Beasley’s leadership, SFASU developed the Forest Resources Institute, the Waters of East Texas Center, and the National Center of Pharmaceutical Crops.  He was selected as a fellow in the Society of American Foresters in 1997 and served as the president of the Texas Forestry Association in 2000.<br /> Dr. Beasley graduated from Stephen F. Austin State University with a Bachelor’s Degree in Forest Management and from the University of Arizona with a Doctorate in Watershed Management.  He is married to Paula Beasley, and they have two children.</p>"},{"name":"Simon W. Henderson, Jr.","dates":"1904-1992","inducted":2001,"image":"henderson-jr-simon-w.jpg","description":"<p>Simon W. Henderson Jr. was born in 1904 to an established lumber family as co-owners of the Angelina County Lumber Co. Henderson is a co-founder of the Lufkin Foundry and Machine Co., later known as Lufkin Industries. He was the first Board Secretary of the Southland Paper Mill when it opened in 1938. The paper mill used southern yellow pine trees to manufacture newsprint. Both industries brought thousands of jobs to Lufkin.<br />Henderson attended Satterwhite Business College. He served on many civic boards, including the Memorial Medical Center board, where he was an original member. He is honored in the Angelina County Business Hall of Fame and served as the president of the Texas Forestry Association in 1955. Henderson was married to Louise Johnson Henderson, and they had five children.</p>"}]

