import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import './Navigation.css';

class Navigation extends Component {

  

  render() {
    const setActive = () => {
      if(window.location.href.includes('/member/')) {
        return true
      }
    }
    return(
      <div className="navigation">
        <ul className="list-inline">
          <li>
            <NavLink exact to="/" className="link" activeClassName="active">
              <div className="line"></div>            
              <div className="active-marker"></div>
              Home
            </NavLink>
          </li>
          <li>
            <NavLink className="link" activeClassName="active" to="/members">
              <div className="line"></div>
              <div className="active-marker"></div>
              Members
            </NavLink>
          </li>
          <li>
            <NavLink to="/member/0" className="link" isActive={setActive} activeClassName="active">
              <div className="line"></div>
              <div className="active-marker"></div>
              Member Profile
            </NavLink>
          </li>
        </ul>
      </div>
    )
  }
}

export default Navigation;


