import React, { Component } from 'react';
import Slider from 'react-slick';
import { Container, Row, Col, Fade } from 'reactstrap';
import DATA from '../data/memberdata.json';

import './MemberProfile.css';

class MemberProfile extends Component {
  state = {
    members: [],
    fadeIn: true
  }

  componentDidMount() {
    this.setState({
      members: DATA
    });
  }

  render() {
    const params = Number(this.props.match.params.id);
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      initialSlide: params
    };
    let slides = this.state.members.map((slide, index) => {
      let createMarkup = () => {
        return {__html: slide.description }
      }
      return (
        <div key={index} className="member-slide">
          <Row>
            <Col md="4">
              <img src={`${process.env.PUBLIC_URL}/member-images/${slide.image}`} alt=""/>
            </Col>
            <Col md="8">
              <h1>{slide.name}</h1>
              <h5>{slide.dates}</h5>
              <h6>Inducted {slide.inducted}</h6>
              <div className="description" dangerouslySetInnerHTML={createMarkup()}></div>
            </Col>
          </Row>
        </div>
      );
    });
    return(
      <div className="member-profile">
        <Fade in={this.state.fadeIn}>
          <Container>
            <Slider {...settings}>{slides}</Slider>
          </Container>
        </Fade>
      </div>
    )
  }
}

export default MemberProfile;


