import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Row, Col, Card, CardImg, CardBody, CardTitle, CardSubtitle, Fade } from 'reactstrap';
import './Members.css';

class Members extends Component {
  state = {
    fadeIn: true
  }

  render() {
    let members = this.props.data.map((member, index) => {
      return(
        <Col key={index} sm="3">
          <Link to={`/member/${index}`}>
            <Card>
              <CardImg top width="100%" src={`${process.env.PUBLIC_URL}/member-images/${member.image}`} />
              <CardBody>
                <CardTitle>{member.name}</CardTitle>
                <CardSubtitle>{member.dates}</CardSubtitle>
              </CardBody>
            </Card>
          </Link>          
        </Col>
      );
    });
    return (
      <div className="members">
        <Fade in={this.state.fadeIn}>
          <Row>
            {members}
          </Row>
        </Fade>
      </div>
    );
  }
}

export default Members;


