import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import DATA from './data/memberdata.json';
import './App.css';

import Navigation from './Components/Navigation';
import AttractLoop from './Components/AttractLoop';
import Home from './Components/Home';
import Members from './Components/Members';
import MemberProfile from './Components/MemberProfile';

class App extends Component {
  state = {
    members: []
  }

  componentDidMount() {
    this.setState({
      members: DATA
    });
    if (window.location !== `${process.env.PUBLIC_URL}/attract`) {
      let t;
      const logout = () => window.location.replace(`${process.env.PUBLIC_URL}/attract`);

      const resetTimer = () => {
          clearTimeout(t);
          t = setTimeout(logout, 300000);  // time is in milliseconds
      }
      
      window.onload = resetTimer;
      window.onmousemove = resetTimer;
      window.onmousedown = resetTimer; // catches touchscreen presses
      window.onclick = resetTimer;     // catches touchpad clicks
      window.onscroll = resetTimer;    // catches scrolling with arrow keys
      window.onkeypress = resetTimer;
    }
  }
  render() {
    return (
      <Router>
        <div className="App">
          <Navigation />
          <Route exact path="/" component={Home} />
          <Route path="/attract" component={AttractLoop} />
          <Route path="/members" render={() => <Members data={this.state.members} />}/>
          <Route path="/member/:id" component={MemberProfile} />
        </div>
      </Router>
    );
  }
}

export default App;



